@media screen and (max-width: 895px) {
.mainContainer{
    position: fixed;
    z-index: 10;
    left: 5%;
    right: 5%;
    top: 20%;
    background-color: white;
    width: 90%;
    border: 1px solid rgba(0, 0, 0, 0.0497193);
    box-sizing: border-box;
    border-radius: 8px;

}
ul{
    padding: 0px;
}
ul li{
    width: 100%;
    height: 70px;
    list-style: none;
    display: flex;
    align-items: center;
    padding-left: 24px;
    a{
        text-decoration: none;
        color: black;
        font-weight: medium;
        font-size: 18px;
    }
}
li + li{
    border-top: 1px solid rgba(47, 47, 47, 0.1);
}
}