.navbar {
  width: 100%;
  height: 280px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#000000), to(rgba(0, 0, 0, 0.0001)));
  background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.0001) 100%);
}

.navlist ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.navlist ul li {
  list-style: none;
  padding-left: 32px;
}

.navlist ul a {
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.hambugerContainer {
  display: none;
}

@media screen and (max-width: 895px) {
  .logo {
    position: relative;
    z-index: 2;
  }
  .navbar {
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  .hambugerContainer {
    display: inline-block;
    position: relative;
    left: -9%;
  }
  .navlist {
    display: none;
  }
  .logo {
    position: relative;
    left: 9%;
  }
  .hambugerContainer input {
    -webkit-appearance: none;
    background-color: transparent;
    background-image: url("C:/Users/kacem/crowdfunding-page/public/icon-hamburger.svg");
    border: none;
    width: 16px;
    height: 15px;
    cursor: pointer;
  }
  .hambugerContainer input:checked {
    position: relative;
    z-index: 2;
    background-image: url("C:/Users/kacem/crowdfunding-page/public/icon-close-menu.svg");
    background-repeat: no-repeat;
    width: 16px;
    height: 15px;
  }
}
