.mainContainer{
    width: 52%;
    border: 1px solid rgba(0, 0, 0, 0.0497193);
    box-sizing: border-box;
    border-radius: 8px;
    padding: 48px;
    background-color: white;
    .mainTextContainer{
        padding-bottom: 40px 40px 40px 20px;
    }
    .mainText{
        padding-bottom: 40px;
        padding-top: 35px;  
    }
}

@media screen and (max-width: 895px) {
    .mainContainer{
        width: 87.2%;
        padding: 24px;
    }
}