body{
    font-family: 'Commissioner';
    background-color: #FAFAFA;
    font-size: clamp(14px, 1.1vw,16px);
    color:  #7A7A7A;
    font-style: normal;
    font-weight: normal;
    line-height: 30px;
    margin: 0px;
    background-image: url('C:/Users/kacem/crowdfunding-page/public/image-hero-desktop.jpg');
    background-repeat: no-repeat;
    background-size: 100% 400px;
    h1{
        font-size: clamp(20px,2vw,24px)
    }
    h2{
        color: black;
        margin: 0;
    }
    h3{
        font-weight: bold;
        color: black;
    }
}
.abt-project{
    display: grid;
    place-items: center;
}
@media screen and (max-width: 895px) {
    .body{
        background-image: url("C:/Users/kacem/crowdfunding-page/public/image-hero-mobile.jpg");
        background-size: 100% 300px;
    }
}