.recordContainer {
  width: 52%;
  border: 1px solid rgba(0, 0, 0, 0.0497193);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 8px;
  padding: 48px;
  background-color: white;
  margin-bottom: 24px;
}

.dataContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 37px;
}

.bar {
  width: 100%;
  height: 12px;
  background-color: #f4f4f4;
  border-radius: 33.5px;
}

.progression {
  width: 75%;
  height: 100%;
  background-color: #3CB3AB;
  border-radius: 33.5px;
}

.title {
  color: black;
  font-size: 32px;
  font-weight: bold;
}

p {
  margin: 0;
}

hr {
  width: 1px;
  height: 63px;
  background-color: #B1B1B1;
  border: none;
  margin: 0;
  margin-left: 42px;
  margin-right: 42px;
}

.valueContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

@media screen and (max-width: 895px) {
  .recordContainer {
    width: 87.2%;
  }
  .dataContainer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
  }
  hr {
    width: 63px;
    height: 1px;
    background-color: #B1B1B1;
    border: none;
    margin: 0;
    margin-bottom: 42px;
    margin-top: 42px;
  }
}
